@use "../abstract" as *;

.main-form__title {
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  padding: 0;
  color: $black;
  margin-top: 27px;
  margin-bottom: 37px;
}

.main-form__description {
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $black;
  margin: 0;
  margin-bottom: 40px;
}

.main-form__required {
  font-family: $arial;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $black;
}

.fieldset-list-radio-wrapper {
  display: block;

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-around;
  }
}
